* {
  box-sizing: border-box;
}

html {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  line-height: 14px;
}

body {
  margin: 0;
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  color: #060b35;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: 0;
  margin-bottom: 16px;
}

.card__rating {
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 24px;
  display: flex;
}

.card__views {
  color: #060b35;
  margin: 0;
  display: inline;
}

.card__price-text {
  clear: both;
  color: #616070;
  margin: 0 auto;
  padding: 0;
  font-size: 12px;
  line-height: 18px;
  display: inline-block;
}

.card__price-price {
  float: right;
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
  display: inline;
}

.card__button {
  box-sizing: border-box;
  text-align: center;
  text-transform: uppercase;
